.events {
  padding-top: 100px;
  background-color: #212326;
  @media (max-width: 1100px) {
    padding-top: 60px;
    padding-bottom: 36px;
  }
}
.events__title {
  margin-bottom: 67px;
  @media (max-width: 1100px) {
    margin-bottom: 27px;
  }
}
.events__carousel {
  width: 100%;
  max-width: 1032px;
  margin-right: auto;
  margin-left: auto;
}

.events__gallery {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  row-gap: 30px;
  column-gap: 30px;
  counter-reset: cards;
  @media (max-width: 992px) {
    display: block;
  }
}

.events__gallery > .card {
  --padding-horizontal: 38px;
  grid-row: var(--grid-row-start) / var(--grid-row-end);
  counter-increment: card;
  @media (max-width: 992px) {
    margin-bottom: 14px;
  }
  @media (max-width: 576px) {
    --padding-horizontal: 15px;
  }
}

.events__gallery > .card:nth-of-type(6n + 6) {
  grid-column: 8 / 13;
}
.events__gallery > .card:nth-of-type(6n + 5) {
  grid-column: 1 / 8;
}
.events__gallery > .card:nth-of-type(6n + 4) {
  grid-column: 8 / 13;
}
.events__gallery > .card:nth-of-type(6n + 3) {
  grid-column: 1 / 6;
}
.events__gallery > .card:nth-of-type(6n + 2) {
  grid-column: 6 / 13;
}
.events__gallery > .card:nth-of-type(1),
.events__gallery > .card:nth-of-type(6n + 1) {
  grid-column: 1 / 6;
}

.events__gallery > .card--no-pic .card__title {
  font-size: 19px;
  line-height: 126%;
  @media (max-width: 992px) {
    margin-bottom: 6px;
    font-size: 15px;
  }
}
.events__gallery > .card--no-pic .card__desc {
  @media (min-width: 993px) {
    font-size: 12px;
    font-weight: 300;
    line-height: 150%;
  }
}
.events__gallery > .card--no-pic .card__pic {
  height: 280px;
  @media (max-width: 992px) {
    height: 150px;
  }
}
.events__gallery > .card--no-pic .card__meta {
  @media (min-width: 993px) {
    padding-right: 20px;
    padding-bottom: 18px;
    padding-left: 21px;
  }
}
.events__gallery > .card--no-pic .card__meta-bullet {
  @media (min-width: 993px) {
    margin-bottom: 3px;
    font-size: 13px;
    line-height: 150%;
  }
}

.events__gallery > .card--no-pic .card__meta-bullet--time {
  padding-top: 2px;
}

.events-page {
  padding-bottom: 126px;
  @media (max-width: 576px) {
    padding-bottom: 62px;
  }
}
